#examples {
  margin-top: 4rem;
  font-size: large;
  text-align: center;
  display: flex;
  color: black;
  list-style: none;
  gap: 1rem;
  margin-bottom: 5rem;
  button {
    width: 250px;
    background-color: #7bb878;
    color: black;
    padding: 14px 28px;
    font-size: 16px;
    font-weight: 600;
    cursor: pointer;
    border-radius: 1rem;
  }
  button:hover{
    background-color: #84b581;
    color: black;
  }

}

@media only screen and (max-width: 600px) {
  #examples {
    display: contents;
    flex-direction: column;
  }
  button {
    margin-right: 2rem;
    margin-top: 0.5rem;
  }
}
