.header {
  display: flex;
  justify-content: space-evenly;
  color: white;
  list-style: none;
  gap: 20px;
  font-size: small;
  img {
    width: 200px;
    height: 200px;
  }
}

.intro{
  font-size: 1.5em;
  font-weight: normal;
  font-style: normal;

  .name{
    font-size: 3em;
    font-weight: 600;
    font-style: normal;
  }
}

.description{
  font-size: 1.5em;
  max-width: 90%;
}

@media only screen and (max-width: 600px) {
  .header {
    margin-bottom: 3rem;
    display: flex;
    flex-direction: column;
  }
  .text-wrapper{
    align-items: center;
  }
  .intro{
    font-size: 1.5em;
  
    .name{
      font-size: 2em;
    }
  }
  .description{
    max-width: 100%;
  }
}

.text-wrapper{
  align-self: center;
  display: flex;
  flex-direction: column;
  h1{
    margin: 0px;
  }
}